import { Component, OnInit } from '@angular/core';

@Component({
  selector: 'app-root',
  templateUrl: './app.component.html',
  styleUrls: ['./app.component.css']
})
export class AppComponent implements OnInit {
  canvas: any;
  canvasCtx: any;
  interVal: any;
  ballX: number;
  ballY: number;
  ballDX: number = 2;
  ballDY: number = -2;
  ballRadius: number = 10;

  //paddle
  paddleHeight = 10;
  paddleWidth = 75;
  paddleX: number;
  rightPressed: boolean = false;
  leftPressed: boolean = false;

  //bricks
  brickRowCount = 3;
  brickColumnCount = 5;
  brickWidth = 75;
  brickHeight = 20;
  brickPadding = 10;
  brickOffsetTop = 30;
  brickOffsetLeft = 30;
  bricks = [];

  //score
  score = 0;

  //lives
  lives = 3;

  ngOnInit(): void {

    this.canvas = document.getElementById("myCanvas");
    this.canvasCtx = this.canvas.getContext("2d");
    this.ballX = this.canvas.width / 2;
    this.ballY = this.canvas.height - 30;
    this.paddleX = (this.canvas.width - this.paddleWidth) / 2;

    document.addEventListener("keydown", this.keyDownHandler.bind(this), false);
    document.addEventListener("keyup", this.keyUpHandler.bind(this), false);
    document.addEventListener("mousemove", this.mouseMoveHandler.bind(this), false);

    var bricks = [];
    for (var c = 0; c < this.brickColumnCount; c++) {
      bricks[c] = [];
      for (var r = 0; r < this.brickRowCount; r++) {
        bricks[c][r] = { x: 0, y: 0, status: 1 };
      }
    }
    this.bricks = bricks;
    this.draw();
  }

  title = 'breakup';

  mouseMoveHandler(e) {
    var relativeX = e.clientX - this.canvas.offsetLeft;
    if (relativeX > 0 && relativeX < this.canvas.width) {
      this.paddleX = relativeX - this.paddleWidth / 2;
    }
  }
  canvasClickHandler() {
    this.stopAnnimation();
  }
  canvasDoubleClkHandler() {
    this.playAnimation();
  }
  playAnimation() {
    this.interVal = setInterval(this.draw.bind(this), 10);
  }
  stopAnnimation() {
    if (this.interVal) clearInterval(this.interVal);
  }

  draw() {

    this.canvasCtx.clearRect(0, 0, this.canvas.width, this.canvas.height);
    this.drawBricks();
    this.drawBall();
    this.ballX += this.ballDX;
    this.ballY += this.ballDY;

    if (this.ballX + this.ballDX > this.canvas.width - this.ballRadius || this.ballX + this.ballDX < this.ballRadius) {
      this.ballDX = -this.ballDX;
    }

    if (this.ballY + this.ballDY < this.ballRadius) {
      this.ballDY = -this.ballDY;
    } else if (this.ballY + this.ballDY > this.canvas.height - this.ballRadius) {
      if (this.ballX > this.paddleX && this.ballX < this.paddleX + this.paddleWidth) {
        this.ballDY = -this.ballDY;
      }
      else {
        this.lives--;
        if (!this.lives) {
          alert("GAME OVER");
          document.location.reload();
          this.stopAnnimation();
        }
        else {
          this.ballDX = this.canvas.width / 2;
          this.ballY = this.canvas.height - 30;
          this.ballDX = 2;
          this.ballDY = -2;
          this.paddleX = (this.canvas.width - this.paddleWidth) / 2;
        }
      }
    }

    this.drawPaddle();
    this.drawScore();
    this.drawLives();
    this.drawInstruction();
    this.collisionDetection();

    if (this.rightPressed && this.paddleX < this.canvas.width - this.paddleWidth) {
      this.paddleX += 7;
    } else if (this.leftPressed && this.paddleX > 0) {
      this.paddleX -= 7;
    }

  }

  drawBall() {

    this.canvasCtx.beginPath();
    console.count("drawing");

    this.canvasCtx.arc(this.ballX, this.ballY, this.ballRadius, 0, Math.PI * 2);
    this.canvasCtx.fillStyle = "#0095DD";
    this.canvasCtx.fill();
    this.canvasCtx.closePath();

  }

  drawPaddle() {
    this.canvasCtx.beginPath();
    this.canvasCtx.rect(this.paddleX, this.canvas.height - this.paddleHeight, this.paddleWidth, this.paddleHeight);
    this.canvasCtx.fillStyle = "#0095DD";
    this.canvasCtx.fill();
    this.canvasCtx.closePath();
  }

  drawBricks() {
    var bricks = this.bricks;
    var ctx = this.canvasCtx;
    for (var c = 0; c < this.brickColumnCount; c++) {
      for (var r = 0; r < this.brickRowCount; r++) {
        if (bricks[c][r].status == 1) {

          var brickX = (c * (this.brickWidth + this.brickPadding)) + this.brickOffsetLeft;
          var brickY = (r * (this.brickHeight + this.brickPadding)) + this.brickOffsetTop;
          bricks[c][r].x = brickX;
          bricks[c][r].y = brickY;

          ctx.beginPath();
          ctx.rect(brickX, brickY, this.brickWidth, this.brickHeight);
          ctx.fillStyle = "#0095DD";
          ctx.fill();
          ctx.closePath();
        }
      }
    }
  }

  collisionDetection() {
    for (var c = 0; c < this.brickColumnCount; c++) {
      for (var r = 0; r < this.brickRowCount; r++) {
        var b = this.bricks[c][r];
        if (b.status == 1) {
          if (this.ballX > b.x && this.ballX < b.x + this.brickWidth && this.ballY > b.y && this.ballY < b.y + this.brickHeight) {
            this.ballDY = -this.ballDY;
            b.status = 0;
            this.score++;

            if (this.score == this.brickRowCount * this.brickColumnCount) {
              alert("YOU WIN, CONGRATULATIONS!");
              document.location.reload();
              this.stopAnnimation();
            }
          }
        }
      }
    }
  }

  drawScore() {
    var ctx = this.canvasCtx;
    ctx.font = "16px Arial";
    ctx.fillStyle = "#0095DD";
    ctx.fillText("Score: " + this.score, 8, 20);
  }

  drawInstruction() {
    var ctx = this.canvasCtx;
    ctx.font = "16px Arial";
    ctx.fillStyle = "#291111";
    ctx.fillText("Double tap to start! Tap to pause.", 100, 20);
  }
  drawLives() {
    var ctx = this.canvasCtx;
    ctx.font = "16px Arial";
    ctx.fillStyle = "#0095DD";
    ctx.fillText("Lives: " + this.lives, this.canvas.width - 65, 20);
  }

  tapHandler(key: string) {

    if (key === 'R') {
      this.rightPressed = true;
    }
    else if (key == 'L') {
      this.leftPressed = true;
    }
  }
  keyDownHandler(e) {
    if (e.key == "Right" || e.key == "ArrowRight") {
      this.rightPressed = true;
    }
    else if (e.key == "Left" || e.key == "ArrowLeft") {
      this.leftPressed = true;
    }
  }

  keyUpHandler(e) {
    if (e.key == "Right" || e.key == "ArrowRight") {
      this.rightPressed = false;
    }
    else if (e.key == "Left" || e.key == "ArrowLeft") {
      this.leftPressed = false;
    }
  }

}
